let aloneActionArr = [];
function aloneAction(number = 0, name1 = '') {
    aloneActionArr = [
        `<span class='member-name'>${name1}</span>    , ,     `,
        `<span class='member-name'>${name1}</span>    `,
        `<span class='member-name'>${name1}</span>    ,      `,
        `<span class='member-name'>${name1}</span>  ,   `,
    ]
    return aloneActionArr[number];
}

aloneAction();


aloneAction.caseLength = aloneActionArr.length;

export default aloneAction;